	module.exports = {"data":{"challengeNode":{"title":"Constructing Strings with Variables","description":["有时候你需要创建一个类似<a href=\"https://en.wikipedia.org/wiki/Mad_Libs\" target=\"_blank\">Mad Libs</a>(填词游戏）风格的字符串。通过使用连接运算符<code> + </code>，你可以插入一个或多个变量来组成一个字符串。","<hr>","把你的名字赋值给变量<code>myName</code>，然后把变量<code>myName</code>插入到字符串<code>\"My name is \"</code>和<code>\" and I am well!\"</code>之间，并把连接后的结果赋值给变量<code>myStr</code>。 "],"challengeType":1,"fields":{"slug":"/javascript-algorithms-and-data-structures/basic-javascript/constructing-strings-with-variables","blockName":"Basic JavaScript","tests":[{"text":"<code>myName</code>至少要包含三个字符","testString":"assert(typeof myName !== 'undefined' && myName.length > 2, '<code>myName</code>至少要包含三个字符');"},{"text":"使用两个<code>+</code>操作符创建包含<code>myName</code>的<code>myStr</code>变量","testString":"assert(code.match(/[\"']\\s*\\+\\s*myName\\s*\\+\\s*[\"']/g).length > 0, '使用两个<code>+</code>操作符创建包含<code>myName</code>的<code>myStr</code>变量');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"// 示例\nvar ourName = \"freeCodeCamp\";\nvar ourStr = \"Hello, our name is \" + ourName + \", how are you?\";\n\n// 请把你的代码写在这条注释以下\nvar myName;\nvar myStr;\n\n","head":"","tail":"(function(){\n  var output = [];\n  if(typeof myName === 'string') {\n    output.push('myName = \"' + myName + '\"');\n  } else {\n    output.push('myName is not a string');\n  }\n  if(typeof myStr === 'string') {\n    output.push('myStr = \"' + myStr + '\"');\n  } else {\n    output.push('myStr is not a string');\n  }\n  return output.join('\\n');\n})();"},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/javascript-algorithms-and-data-structures/basic-javascript/appending-variables-to-strings","id":"56533eb9ac21ba0edf2244b9"},"slug":"/javascript-algorithms-and-data-structures/basic-javascript/constructing-strings-with-variables"}}

